











import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component
export default class extends Vue {
  @Prop({
    type: Object,
    default: undefined,
  })
  errorProp?: Error;

  private get err(): Error {
    if (this.errorProp) return this.errorProp;
    const { name, message } = this.$route.query;
    if (!name || !message)
      return { message: "Erro desconhecido", name: "Erro desconhecido" };
    const n = name as string;
    const msg = message as string;
    return { message: msg, name: n };
  }

  private backPage() {
    history.go(-2);
  }
}
